<template>
  <div id="mocktest">
    <div class = "container">
      <div class="row justify-content-md-center" v-for="(question, index) in questions">
        <div class="coloredCol col col-lg-1 border" v-bind:class="[colorClass[index]]"></div>
        <div class="questionCol col col-lg-5 border">{{question.question}}</div>
        <div class="answersCol col col-lg-5 border">
          <div class="form-check" v-for="answer in question.answers">
            <label>
              <input class="form-check-input" :name="index" :value="answer.pointer" type="radio" :disabled="showCorrectAnswers" v-model="answers[index].givenAnswer">
              <span>{{answer.pointer}}- {{answer.answer}}</span>
            </label>
          </div>
        </div>
        <div class="resultCol col col-lg-1 border">
          <div v-show="showCorrectAnswers">{{answers[index].correctAnswer}}</div>
        </div>
      </div>

      <div class="row justify-content-md-center">
        <div id="getResultsButton" class="col col-6">
          <button v-on:click="getResults">Get Results</button>
        </div>
      </div>
      <router-view/>
    </div>
  </div>
</template>

<script>
import router from '../router'
export default {
  name: 'MockTest',
  props: ["allQuestions", "nQuestions", "correctAnswersPoints", "noAnswersPoints", "wrongAnswersPoints"],
  data () {
    return {
      questions: [],
      answers:[],
      showCorrectAnswers: false,
      colorClass:[]
    }
  },

  mounted(){

    if (!this.allQuestions) {
      this.$emit('refresh')
    }
    else{
      this.allQuestions = this.shuffle(this.allQuestions)
      
      for ( let i = 0; i < this.nQuestions; i++){
        let chosenQuestion = this.allQuestions.pop()
        this.questions.push(chosenQuestion)
        this.answers.push({
          "givenAnswer" : null,
          "correctAnswer": chosenQuestion.correctAnswer
        })
      }
    }
  },

  methods: {

    shuffle(array) {
      let counter = array.length;

      // While there are elements in the array
      while (counter > 0) {
          // Pick a random index
          let index = Math.floor(Math.random() * counter);

          // Decrease counter by 1
          counter--;

          // And swap the last element with it
          let temp = array[counter];
          array[counter] = array[index];
          array[index] = temp;
      }
      return array;
    },

    getResults(){
      router.push({ name: 'Results', params: {answers: this.answers, nQuestions: this.nQuestions, correctAnswersPoints: this.correctAnswersPoints, noAnswersPoints: this.noAnswersPoints, wrongAnswersPoints: this.wrongAnswersPoints}})
      this.showCorrectAnswers = true
      for(let i = 0; i < this.answers.length; i++){
        if (!this.answers[i].givenAnswer) {
          this.answers[i].correc
          this.colorClass.push("background bg-warning")
        }
        else if (this.answers[i].givenAnswer == this.answers[i].correctAnswer) this.colorClass.push("background bg-success")
        else this.colorClass.push("background bg-danger")     
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

input:checked + span{
  font-weight: bold
}

h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



