<template>
  <div id="app">
    <div id = "header" class = "container">
        <div class="row justify-content-md-center">
          <!-- <div class="col col-lg-6"><input id="csvFile" type="text" placeholder="csv File" ></div> -->
          <div class="col"><input v-model="nQuestions" type="text" placeholder="N° Questions" ></div>
        </div>
        <div class="row justify-content-md-center">
          <div class="col col-lg-4"><input v-model="correctAnswersPoints" type="text" placeholder="Points correct answer" ></div>
          <div class="col col-lg-4"><input v-model="wrongAnswersPoints" type="text" placeholder="Points wrong answer" ></div>
          <div class="col col-lg-4"><input v-model="noAnswersPoints" type="text" placeholder="Points no answer" ></div>
        </div>
        <div class="row justify-content-md-center">
          <div v-on:click="createMockTest" class="col" v-show="csvParsed"><button :disabled="mockTestCreated">Create Mock Test</button></div>
        </div>
    </div>
    <DragAndDrop @parsed="passFileData" v-show="!mockTestCreated" ref="dragAndDropComp"/>
    <div id = "main">
      <router-view  @refresh="clear" />
    </div>
    <div id = "footer">
      <div class="row justify-content-md-center">
        <div id="clearButton" class="col col-6">
          <button v-on:click="clear">Clear</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import DragAndDrop from './components/DragAndDrop'
import router from './router'
export default {
  name: "App",
  components: {
    DragAndDrop
  },
  data () {
    return {
      nQuestions: null,
      correctAnswersPoints: null,
      noAnswersPoints: null,
      wrongAnswersPoints: null,
      mockTestCreated: false,
      csvParsed: null,
      allQuestions: false
    }
  },

  methods: {
    createMockTest(){
      this.mockTestCreated = true
      router.push({ name: 'MockTest', params: {allQuestions: this.allQuestions, nQuestions: this.nQuestions, correctAnswersPoints: this.correctAnswersPoints, noAnswersPoints: this.noAnswersPoints, wrongAnswersPoints: this.wrongAnswersPoints }})
    },   
    
    clear(){
      this.$refs.dragAndDropComp.clear()
      this.mockTestCreated= false
      this.nQuestions= null
      this.correctAnswersPoints= null
      this.noAnswersPoints= null
      this.wrongAnswersPoints= null
      this.csvParsed= false
      this.allQuestions= null
      router.push({ path : '/'})
    },

    passFileData(value){
      this.allQuestions = value
      this.csvParsed = true
    }

  }

}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



